import { TrophyIcon } from '@heroicons/react/24/outline'

export default function VisualBracket({ teams = [] }) {
  const totalTeams = teams.length
  const playoffTeamCount = totalTeams // All teams participate (11 teams)
  const playoffTeams = teams
  
  // March Madness style: 11 teams = 5 byes + 6 first round teams (3 games)
  const byeCount = 5 // Top 5 teams get byes
  const byeTeams = playoffTeams.slice(0, byeCount)
  const firstRoundTeams = playoffTeams.slice(byeCount)

  const TeamCard = ({ team, isBye = false, isWinner = false, size = 'normal' }) => (
    <div className={`flex items-center space-x-2 p-2 rounded border ${
      size === 'small' ? 'text-xs' : 'text-sm'
    } ${
      isWinner ? 'bg-green-100 border-green-300 font-semibold' :
      isBye ? 'bg-blue-50 border-blue-200' :
      'bg-white border-gray-200 shadow-sm'
    }`}>
      <div className={`${size === 'small' ? 'w-5 h-5' : 'w-6 h-6'} rounded-full flex items-center justify-center text-xs font-bold text-white ${
        team?.seed === 1 ? 'bg-yellow-500' :
        team?.seed === 2 ? 'bg-gray-400' :
        team?.seed === 3 ? 'bg-orange-600' : 'bg-blue-600'
      }`}>
        {team?.seed || '?'}
      </div>
      <span className="truncate flex-1">{team?.name || 'TBD'}</span>
      {isBye && <span className="text-xs bg-blue-200 px-1 rounded">BYE</span>}
    </div>
  )

  const BracketLine = ({ type = 'horizontal', length = 'normal' }) => {
    if (type === 'horizontal') {
      return <div className={`bg-gray-300 h-0.5 ${length === 'short' ? 'w-4' : 'w-8'}`}></div>
    }
    if (type === 'vertical') {
      return <div className={`bg-gray-300 w-0.5 ${length === 'short' ? 'h-4' : 'h-8'}`}></div>
    }
    if (type === 'corner-right') {
      return (
        <div className="relative">
          <div className="bg-gray-300 h-0.5 w-4"></div>
          <div className="bg-gray-300 w-0.5 h-4 absolute top-0 right-0"></div>
        </div>
      )
    }
    if (type === 'corner-left') {
      return (
        <div className="relative">
          <div className="bg-gray-300 h-0.5 w-4"></div>
          <div className="bg-gray-300 w-0.5 h-4 absolute top-0 left-0"></div>
        </div>
      )
    }
    return null
  }

  // 11-team March Madness style bracket with proper tree layout
  if (playoffTeamCount >= 4) {
    // Find specific teams for custom matchups
    const palakers = playoffTeams.find(t => t.name && t.name.toLowerCase().includes('palakers'))
    const indianaBones = playoffTeams.find(t => t.name && t.name.toLowerCase().includes('indiana') && t.name.toLowerCase().includes('bones'))
    const diddyParty = playoffTeams.find(t => t.name && t.name.toLowerCase().includes('diddy'))
    const redeemTeam = playoffTeams.find(t => t.name && t.name.toLowerCase().includes('redeem'))
    
    const usedTeams = new Set([
      palakers?.id, indianaBones?.id, diddyParty?.id, redeemTeam?.id
    ].filter(Boolean))
    
    const remainingTeams = firstRoundTeams.filter(team => !usedTeams.has(team.id))

    return (
      <div className="bg-white rounded-lg border border-gray-200">
        <h3 className="text-lg font-bold text-center mb-6 p-6 pb-0">March Madness Tournament Bracket - {playoffTeamCount} Teams</h3>
        
        {/* Desktop bracket - hidden on mobile */}
        <div className="hidden lg:block p-6 overflow-x-auto">
          <div className="relative min-w-[1200px]">
          <div className="grid grid-cols-7 gap-4 items-center">
            
            {/* ROUND 1 - First Round Games */}
            <div className="space-y-8">
              <div className="text-center font-semibold text-gray-600 mb-4">First Round</div>
              <div className="text-center text-xs text-blue-600 mb-4">August 10th</div>
              
              {/* Game 1: Palakers vs Indiana Bones */}
              {palakers && indianaBones && (
                <div className="space-y-2 bg-yellow-50 border border-yellow-300 rounded p-3">
                  <TeamCard team={palakers} size="small" />
                  <TeamCard team={indianaBones} size="small" />
                  <div className="text-center text-xs text-yellow-700">6:00 PM</div>
                </div>
              )}
              
              <div className="h-8"></div>
              
              {/* Game 2: Diddy Party vs Redeem Team */}
              {diddyParty && redeemTeam && (
                <div className="space-y-2 bg-green-50 border border-green-300 rounded p-3">
                  <TeamCard team={diddyParty} size="small" />
                  <TeamCard team={redeemTeam} size="small" />
                  <div className="text-center text-xs text-green-700">7:30 PM</div>
                </div>
              )}
              
              <div className="h-8"></div>
              
              {/* Game 3: Remaining teams */}
              {remainingTeams.length >= 2 && (
                <div className="space-y-2 bg-gray-50 border border-gray-300 rounded p-3">
                  <TeamCard team={remainingTeams[0]} size="small" />
                  <TeamCard team={remainingTeams[1]} size="small" />
                  <div className="text-center text-xs text-gray-600">9:00 PM</div>
                </div>
              )}
            </div>

            {/* CONNECTION LINES 1 */}
            <div className="flex flex-col justify-center space-y-16">
              <BracketLine type="horizontal" />
              <BracketLine type="horizontal" />
              <BracketLine type="horizontal" />
            </div>

            {/* ROUND 2 - Quarterfinals */}
            <div className="space-y-6">
              <div className="text-center font-semibold text-gray-600 mb-4">Quarterfinals</div>
              
              {/* QF1: #1 vs Winner Game 1 */}
              <div className="space-y-2 border border-gray-200 rounded p-3 bg-white">
                <TeamCard team={byeTeams.find(t => t.seed === 1)} isBye size="small" />
                <div className="text-xs text-gray-500 text-center">vs Winner Game 1</div>
              </div>
              
              {/* QF2: #2 vs Winner Game 2 */}
              <div className="space-y-2 border border-gray-200 rounded p-3 bg-white">
                <TeamCard team={byeTeams.find(t => t.seed === 2)} isBye size="small" />
                <div className="text-xs text-gray-500 text-center">vs Winner Game 2</div>
              </div>
              
              {/* QF3: #3 vs Winner Game 3 */}
              <div className="space-y-2 border border-gray-200 rounded p-3 bg-white">
                <TeamCard team={byeTeams.find(t => t.seed === 3)} isBye size="small" />
                <div className="text-xs text-gray-500 text-center">vs Winner Game 3</div>
              </div>
              
              {/* QF4: #4 vs #5 */}
              <div className="space-y-2 border border-gray-200 rounded p-3 bg-white">
                <TeamCard team={byeTeams.find(t => t.seed === 4)} isBye size="small" />
                <TeamCard team={byeTeams.find(t => t.seed === 5)} isBye size="small" />
              </div>
            </div>

            {/* CONNECTION LINES 2 */}
            <div className="flex flex-col justify-center space-y-24">
              <BracketLine type="horizontal" />
              <BracketLine type="horizontal" />
            </div>

            {/* ROUND 3 - Semifinals */}
            <div className="space-y-12">
              <div className="text-center font-semibold text-gray-600 mb-4">Semifinals</div>
              
              <div className="space-y-2 border border-gray-200 rounded p-3 bg-white">
                <div className="text-xs text-gray-500 text-center">QF1 Winner</div>
                <div className="text-center text-gray-400">vs</div>
                <div className="text-xs text-gray-500 text-center">QF2 Winner</div>
              </div>
              
              <div className="space-y-2 border border-gray-200 rounded p-3 bg-white">
                <div className="text-xs text-gray-500 text-center">QF3 Winner</div>
                <div className="text-center text-gray-400">vs</div>
                <div className="text-xs text-gray-500 text-center">QF4 Winner</div>
              </div>
            </div>

            {/* CONNECTION LINES 3 */}
            <div className="flex justify-center items-center">
              <BracketLine type="horizontal" />
            </div>

            {/* ROUND 4 - Championship */}
            <div className="space-y-4">
              <div className="text-center font-semibold text-gray-600 mb-4">Championship</div>
              
              <div className="border-2 border-yellow-400 rounded p-4 bg-yellow-50 text-center">
                <TrophyIcon className="h-10 w-10 text-yellow-600 mx-auto mb-2" />
                <div className="text-sm font-semibold text-yellow-800">Final Game</div>
                <div className="text-xs text-yellow-600 mt-1">Winner Takes All</div>
              </div>
            </div>
          </div>
          </div>
        </div>

        {/* Mobile bracket - simplified vertical layout */}
        <div className="lg:hidden p-6">
          <div className="space-y-6">
            {/* First Round */}
            <div>
              <h4 className="font-semibold text-gray-800 mb-3 text-center">First Round - August 10th</h4>
              <div className="space-y-4">
                {/* Game 1: Palakers vs Indiana Bones */}
                {palakers && indianaBones && (
                  <div className="bg-yellow-50 border border-yellow-300 rounded p-3">
                    <div className="text-center text-xs text-yellow-700 mb-2">Game 1 - 6:00 PM</div>
                    <div className="space-y-1">
                      <TeamCard team={palakers} size="small" />
                      <div className="text-center text-gray-500 text-xs">vs</div>
                      <TeamCard team={indianaBones} size="small" />
                    </div>
                  </div>
                )}
                
                {/* Game 2: Diddy Party vs Redeem Team */}
                {diddyParty && redeemTeam && (
                  <div className="bg-green-50 border border-green-300 rounded p-3">
                    <div className="text-center text-xs text-green-700 mb-2">Game 2 - 7:30 PM</div>
                    <div className="space-y-1">
                      <TeamCard team={diddyParty} size="small" />
                      <div className="text-center text-gray-500 text-xs">vs</div>
                      <TeamCard team={redeemTeam} size="small" />
                    </div>
                  </div>
                )}
                
                {/* Game 3: Remaining teams */}
                {remainingTeams.length >= 2 && (
                  <div className="bg-gray-50 border border-gray-300 rounded p-3">
                    <div className="text-center text-xs text-gray-700 mb-2">Game 3 - 9:00 PM</div>
                    <div className="space-y-1">
                      <TeamCard team={remainingTeams[0]} size="small" />
                      <div className="text-center text-gray-500 text-xs">vs</div>
                      <TeamCard team={remainingTeams[1]} size="small" />
                    </div>
                  </div>
                )}
              </div>
            </div>

            {/* Arrow */}
            <div className="text-center">
              <div className="text-gray-400">↓</div>
            </div>

            {/* Quarterfinals */}
            <div>
              <h4 className="font-semibold text-gray-800 mb-3 text-center">Quarterfinals</h4>
              <div className="grid grid-cols-1 gap-3">
                <div className="border border-gray-200 rounded p-2 text-center text-sm">
                  <div className="font-medium">#1 Seed vs Winner Game 1</div>
                </div>
                <div className="border border-gray-200 rounded p-2 text-center text-sm">
                  <div className="font-medium">#2 Seed vs Winner Game 2</div>
                </div>
                <div className="border border-gray-200 rounded p-2 text-center text-sm">
                  <div className="font-medium">#3 Seed vs Winner Game 3</div>
                </div>
                <div className="border border-gray-200 rounded p-2 text-center text-sm">
                  <div className="font-medium">#4 Seed vs #5 Seed</div>
                </div>
              </div>
            </div>

            {/* Arrow */}
            <div className="text-center">
              <div className="text-gray-400">↓</div>
            </div>

            {/* Semifinals */}
            <div>
              <h4 className="font-semibold text-gray-800 mb-3 text-center">Semifinals</h4>
              <div className="grid grid-cols-1 gap-3">
                <div className="border border-gray-200 rounded p-2 text-center text-sm">
                  <div className="font-medium">QF1 Winner vs QF2 Winner</div>
                </div>
                <div className="border border-gray-200 rounded p-2 text-center text-sm">
                  <div className="font-medium">QF3 Winner vs QF4 Winner</div>
                </div>
              </div>
            </div>

            {/* Arrow */}
            <div className="text-center">
              <div className="text-gray-400">↓</div>
            </div>

            {/* Championship */}
            <div>
              <h4 className="font-semibold text-gray-800 mb-3 text-center">Championship</h4>
              <div className="border-2 border-yellow-400 rounded p-4 bg-yellow-50 text-center">
                <TrophyIcon className="h-8 w-8 text-yellow-600 mx-auto mb-2" />
                <div className="text-sm font-semibold text-yellow-800">Final Game</div>
                <div className="text-xs text-yellow-600">Winner Takes All</div>
              </div>
            </div>
          </div>
        </div>

        {/* Legend */}
        <div className="px-6 pb-6">
          <div className="pt-4 border-t border-gray-200">
            <div className="flex justify-center space-x-4 text-xs flex-wrap gap-2">
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-blue-50 border border-blue-200 rounded"></div>
                <span>Bye Team</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-yellow-50 border border-yellow-300 rounded"></div>
                <span>Palakers Game</span>
              </div>
              <div className="flex items-center space-x-2">
                <div className="w-3 h-3 bg-green-50 border border-green-300 rounded"></div>
                <span>Diddy Party Game</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }

  // Default: Not enough teams
  return (
    <div className="bg-white p-6 rounded-lg border border-gray-200 text-center">
      <TrophyIcon className="h-12 w-12 text-gray-400 mx-auto mb-4" />
      <h3 className="text-lg font-medium text-gray-900 mb-2">Tournament Bracket</h3>
      <p className="text-gray-600">
        Need at least 4 teams to generate a playoff bracket
      </p>
    </div>
  )
}